const getType = (obj) => Object.prototype.toString.call(obj).slice(8, -1);
function isArray(obj) {
    return getType(obj) === "Array";
}
function isObject(obj) {
    return getType(obj) === "Object";
}
function isString(obj) {
    return getType(obj) === "String";
}
function isNumber(obj) {
    return getType(obj) === "Number" && obj === obj;
}
function isRegExp(obj) {
    return getType(obj) === "RegExp";
}
function isFile(obj) {
    return getType(obj) === "File";
}
function isBlob(obj) {
    return getType(obj) === "Blob";
}
function isUndefined(obj) {
    return obj === undefined;
}
function isFunction(obj) {
    return typeof obj === "function";
}
function isEmptyObject(obj) {
    return isObject(obj) && Object.keys(obj).length === 0;
}

export { isArray, isBlob, isEmptyObject, isFile, isFunction, isNumber, isObject, isRegExp, isString, isUndefined };
