/**
 * memoize
 * @param callback
 * @returns {(...args: any[]) => R}
 */
function memoize(callback) {
    const cache = new Map();
    return function (...args) {
        const key = JSON.stringify(args);
        if (cache.has(key)) {
            return cache.get(key);
        }
        const result = callback.apply(this, args);
        cache.set(key, result);
        return result;
    };
}

export { memoize };
