import { i as ifNotFunction } from './validate-7008d2c6.js';
import './type.js';

/**
 * 节流
 * @param fn {*}  执行函数
 * @param delay {*}  节流时间,毫秒
 * @returns {(...args: any[]) => void}
 */
function throttle(callback, delay = 60) {
    ifNotFunction(callback);
    let flag = true;
    return function (...args) {
        if (flag) {
            flag = false;
            const res = callback.call(this, ...args);
            setTimeout(() => {
                flag = true;
            }, delay);
            return res;
        }
        return undefined;
    };
}

export { throttle };
